/**
 * text
 */
// Alignment
.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.text-justify {
  text-align: justify; }

// Contextual colors
.text-muted {
  color: $text-muted; }
.vertical-align-middle {
  display: inline-block !important;
  vertical-align: middle !important; }
.vertical-align-bottom {
  display: inline-block !important;
  vertical-align: bottom !important; }
.text-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis; }
.text-break-word {
  word-wrap: break-word; }
.nowrap {
  word-wrap: nowrap;
  white-space: nowrap; }
.lower-case {
  text-transform: lowercase; }
.upper-case {
  text-transform: uppercase; }
.capital-case {
  text-transform: capitalize; }
.hidden-text {
  text-indent: -9999px;
  color: transparent; }

/**
