/**
 *line
 */
.line {
  width: 100%;
  height: 1px;
  margin: 10px 0;
  font-size: 0;
  overflow: hidden;
  border-width: 0;
  background-color: $secondary-color; }
.line-dashed {
  border-style: dashed;
  background: transparent; }
.headline {
  border-bottom: 5px solid $black;
  margin-top: 0;
  line-height: 45px; }

.no-line {
  border-width: 0; }
.no-border {
  border-color: transparent !important; }
.no-radius {
  border-radius: 0; }
.block {
  display: block; }
.inline {
  display: inline-block; }
.pull-left {
