/**
 * Flexbox
 */
.flex-container {
  display: flex; }

.flex-dir-col {
  flex-flow: column wrap; }

.flex-dir-row {
  flex-flow: row wrap; }

.flex-space-around {
  justify-content: space-around; }

.flex-space-between {
  justify-content: space-between; }

.flex-align-center {
  align-items: center; }

.flex-it-4 {
  flex: 0 1 25%;
 }  // IE10
_:-ms-input-placeholder, :root .flex-it-4 {
  -ms-flex-preferred-size: 21% !important; }

.flex-it-3 {
  flex: 0 1 33%;
 }  // IE10
_:-ms-input-placeholder, :root .flex-it-3 {
  -ms-flex-preferred-size: 29% !important; }

.flex-it-2 {
  flex: 0 1 50%;
 }  // IE10
_:-ms-input-placeholder, :root .flex-it-2 {
  -ms-flex-preferred-size: 48% !important;

  @supports ( not ( mix-blend-mode: luminosity)) {
    flex: 0 1 49.95%; } }


.flex-it-1 {
  flex: 0 1 100%; }

/**
 * others
 */
.unscroll {
  overflow: hidden !important; }

.clickable {
  cursor: pointer; }

.rotate-45 {
  transform: rotate(45deg); }

.content-box {
  box-sizing: content-box; }

.faded {
  opacity: .3; }

.op0 {
  opacity: 0; }

.line-through {
  text-decoration: line-through; }

.no-decoration {
  text-decoration: none; }


/**
 * Centering
 */
.v-center {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }

.h-center {
  position: absolute;
  left: 50%;
  transform: translateX(-50%); }

.vh-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); }


/**
 * Hidden utilities
 */
.hidden-sm {
  @media (max-width: $screen-sm) {
    display: none !important; } }

.visible-sm {
  @media (max-width: $screen-sm) {
    display: block !important; } }

.hidden {
  display: none !important;
  visibility: hidden !important; }

/**
 * CSS for <sub> and <sup>
 */
sub, sup {
  font-size: 50%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.7em; }

sub {
  bottom: -0.25em; }

[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important; }



/**
 * Lists
 */
.list-none {
  list-style: none; }

/**
 * Clearfix
 */
.cf:before,
.cf:after {
  content: " ";
  display: table; }


.cf:after {
  clear: both; }

/**
 * For IE 6/7 only
 * Include this rule to trigger hasLayout and contain floats.
 */
.cf {
  *zoom: 1; }


/**
 * Shadows
 */

.elevation0 {
  box-shadow: 0 5px 15px 0 rgba(112, 128, 175, 0.2); }

.elevation1 {
  box-shadow: 0 10px 40px 0 rgba($bunting,0.07), 0 2px 9px 0 rgba($bunting,0.06); }

.elevation2 {
  box-shadow: 0 16px 32px 0 rgba($bunting, 0.2); }

// Placeholders
%elevation1 {
  box-shadow: 0 10px 40px 0 rgba($bunting,0.07), 0 2px 9px 0 rgba($bunting,0.06); }

/**
 * For inputs
 */
.apn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

.otn {
  outline: none; }

/**
 * To use with gradient for text
 */
.gradient-text {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }
